"use client"
import React, {ChangeEventHandler, MouseEventHandler} from 'react';
import {observer} from 'mobx-react-lite'
import './Input.scss'


interface IInput {
    type?: string
    placeholder: string
    onChange: ChangeEventHandler<HTMLInputElement>
    value: string | number | readonly string[] | undefined
    icon?: string
    href?: string
    onClick?: MouseEventHandler<HTMLElement> | undefined
    onClickIcon?: MouseEventHandler<HTMLElement> | undefined
    autoFocus?: boolean
    length:number
}

export default observer(({type, placeholder, onChange, value, autoFocus,onClick,onClickIcon,length}: IInput) => {

        return (
            <div className='input w-full mr-1 '>
                <input onClick={onClick} autoFocus={autoFocus} type={type ? type : 'text'} placeholder={placeholder} className='input__input'
                       onChange={onChange}
                       value={value}/>
                    {
                        length !== 0 && <i className="fa-solid fa-xmark" onClick={onClickIcon}/>
                    }

            </div>

        );
    }
)
