import React from 'react';
import {observer} from "mobx-react-lite";
import Input from "../../UI/Input/Input";
import './Filter.scss'
import filterStateStore from "../../../lib/store/filter-state-store";
import MainStateStore from "../../../lib/store/main-state-store";
import {Card} from "../../UI/Card/Card";


export const Filter = observer(() => {
    return (
        <div className=' filter'>
            <div className='sticky z-50 top-0 bg-white p-4'>
                <div className='filter__title-page'>
                    Фильтр

                </div>
                <Input
                    type={'number'}
                    length={filterStateStore.valueSum.length}
                    onClickIcon={() => filterStateStore.setValueSum('')}
                    autoFocus={true}
                    placeholder={'Введите сумму займа'}
                    value={filterStateStore.valueSum}
                    onChange={(e) => filterStateStore.setValueSum(e.target.value)}/>

            </div>
            <div className="page-home__cards p-4">
                {MainStateStore.list.map((item, index) => (
                    Number(filterStateStore.valueSum) <= item.desc.summa_before && filterStateStore.valueSum.length !== 0 &&
                    <Card key={`filter-${item.title + index}`} data={item}/>
                ))}
            </div>

        </div>
    );
})

