import React from 'react';
import {observer} from 'mobx-react-lite'
import './Rate.scss'
import mainStateStore from "../../../lib/store/main-state-store";
import {getCurrentDateInRussianFormat} from "../../../generic/Date/date";
import {CheckingLink} from "../../../generic/Function/CheckingLink";

export default observer(() => {

        return (
            <div>
                <div className='card rate'>
                    {
                        mainStateStore.rateData.map((bet, index) => (
                            <div key={`rate-${bet.title + index}`} className='rate__card'>
                                <div className='rate__title'>
                                    <img src={bet.img} alt={bet.title} className='rate__img'/>
                                    <div>
                                        {bet.title}
                                        <div className='rate__stat'
                                             style={{color: bet.rate.number_seats.includes('+') ? "#19AB4A" : "#FC3F1D"}}>
                                            <i className={`fa-solid mr-2 fa-arrow-${bet.rate.number_seats.includes('+') ? 'up' : 'down'}`}/>
                                            {bet.rate.number_seats}
                                        </div>
                                    </div>
                                </div>
                                <div onClick={() => {
                                    mainStateStore.setModalState(true)
                                    CheckingLink(bet.link)
                                }} className='rate__btn '>
                                    Открыть
                                </div>
                            </div>
                        ))
                    }
                    <div className='rate__date'>
                        от {getCurrentDateInRussianFormat()}
                    </div>
                </div>
            </div>
        );
    }
)

