import React from 'react';
import {observer} from 'mobx-react-lite'
import './SearchForm.scss'
import pageHomeStore from "../../../lib/store/page-home-store";
import Input from "../Input/Input";
import MainStateStore from "../../../lib/store/main-state-store";
import mainStateStore from "../../../lib/store/main-state-store";
import {CheckingLink} from "../../../generic/Function/CheckingLink";


export default observer(() => {

        const filteredData = MainStateStore.fullList.filter((val) => val.title.toLowerCase().includes(pageHomeStore.valueSearch.toLowerCase()));
        const slicedData = filteredData.slice(0, 5)


        return (
            <div className=" search__modal w-full">

                <Input
                    length={pageHomeStore.valueSearch.length}
                    onClickIcon={() => pageHomeStore.setValueSearch('')}
                    autoFocus={true}
                    placeholder={'Найди'}
                    value={pageHomeStore.valueSearch}
                    onChange={(e) => pageHomeStore.setValueSearch(e.target.value)}/>


                {
                    pageHomeStore.valueSearch.length !== 0 ?
                        slicedData.map((bet, index) => (
                            <div key={`search-${bet.title + index}`} className='search__card  no-animation'
                                 onClick={() => {
                                     mainStateStore.setModalState(true)
                                     CheckingLink(bet.link)
                                 }}>
                                <div className='search__title'>
                                    <img src={bet.img} alt={bet.title} className='search__img'/>
                                    {bet.title}
                                </div>
                                <i className="fa-solid fa-magnifying-glass"/>
                            </div>
                        ))
                        :
                        <img
                            className='mt-10'
                            src={'https://img.freepik.com/premium-vector/woman-is-making-observations-with-magnifying-glass_705714-1089.jpg?w=360'}
                            width={512} height={512} alt={'searcg'}/>
                }
            </div>
        );
    }
)
