import {makeAutoObservable} from 'mobx'
import services from "../services/services";
import {Loan} from "../models/loan";


interface IMainStateStore {
    list: Loan[]
    rateData: Loan[]
    fullList: Loan[]
    stateCity: string
    btnClosureStatus: boolean
    modalState: boolean
}

class MainStateStore implements IMainStateStore {
    list: Loan[] = []
    rateData: Loan[] = []
    fullList: Loan[] = []
    stateCity = ''
    btnClosureStatus = false
    modalState = false

    constructor() {
        makeAutoObservable(this)
    }

    setList(val: Loan[]): void {
        this.list = val
    }

    setRateData(val: Loan[]): void {
        this.rateData = val
    }
    setFullList(val: Loan[]): void {
        this.fullList = val
    }

    setStateCity(val: string): void {
        this.stateCity = val
    }

    setBtnClosureStatus(val: boolean): void {
        this.btnClosureStatus = val
    }

    setModalState(val: boolean): void {
        this.modalState = val
    }

    dataLoans(): void {
        services
            .getLoans()
            .then((res) => {
                const rate:Loan[] = []
                const list:Loan[] = []

                res.rate.filter((itemRate) => (
                    res.mainData.filter((itemData) => {
                        if (itemData.id === itemRate) {
                            rate.push(itemData)
                        }
                    })
                ))
                res.list_loans.filter((itemRate) => (
                    res.mainData.filter((itemData) => {
                        if (itemData.id === itemRate) {
                            list.push(itemData)
                        }
                    })
                ))
                this.setFullList(res.mainData)
                this.setRateData(rate)
                this.setList(list)
            })

    }

    apiCheck(): void {
        services
            .getIp()
            .then((res) => {
                this.setStateCity(res)
            })

    }

    btnCheck(): void {
        services
            .getStatusBtn()
            .then((res) => {
                this.setBtnClosureStatus(res)
            })

    }
}

// eslint-disable-next-line import/no-anonymous-default-export
export default new MainStateStore()
