import React from 'react';

interface ITooltip {
    btn?: boolean
    title?: any
    description?: string
    className?: string
}

export const Tooltip = ({btn, title, description,className}: ITooltip) => {
    return (
        <div className={`${className} tooltip tooltip-bottom`} data-tip={description}>
            {
                btn ? <div className="btn">{title}</div> : title
            }
        </div>
    );
};
