import {makeAutoObservable} from 'mobx'


interface IFilterStateStore {
    valueSum: string
}

class FilterStateStore implements IFilterStateStore {
    valueSum = ''

    constructor() {
        makeAutoObservable(this)
    }

    setValueSum(val: string) {
        this.valueSum = val
    }

}

// eslint-disable-next-line import/no-anonymous-default-export
export default new FilterStateStore()
